@use "src/global-styles" as global;

.create {
  padding: 1.5rem 0;
  width: 90%;
  margin: 0 auto;
  .title {
   @extend %title-pages;
  }
  .textContent {
    text-align: left;
    font-size: 1.1rem;
    margin-bottom: 1rem;
  }
  .createContent {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    margin-bottom: 1rem;
  }
  .optionsChartContainer {
    .miniTitle {
      @extend %mini-title;
    }
    .optionsChartInner {
    }
  }
}


@media (max-width: 1024px) {
}

@media (max-width: 768px) {
  .create {
    .createContent {
      .optionsChartContainer {
        .optionsChartInner {
          display: block;
        }
      }
    }
  }
}
